resources['Assembly.vert'] = '#ifdef GL_FRAGMENT_PRECISION_HIGH\nprecision highp float;\n#else\nprecision mediump float;\n#endif\nin float index;\nuniform float numIndices;\nuniform sampler2D samplers[1 + N/2];\nuniform float sheet;\nout vec4 posValue;\nvec2 texCoord[3];\n\n#define W 2048.0\n#define H 2048.0\nvec2 uvPosition (in float i) {\n    return vec2 (mod (i, W) / W + 0.5 / W, floor (i / W) / H + 0.5 / H);\n}\n\nvoid readTex (out vec2 pos[3], out float delta[3], out vec2 values[3*N]) {\n    vec4 s;\n    for (int i = 0; i < 3; i++) {\n        s = texture (samplers[0], texCoord[i]);\n        pos[i] = s.xy;\n        delta[i] = s.z;\n        for (int j = 1; j < N/2; j++) {\n            s = texture (samplers[j], texCoord[i]);\n            values[i*N+2*j-2] = s.xy;\n            values[i*N+2*j-1] = s.zw;\n        }\n    }\n}\n\nbool valid (in vec2 position[3], in float delta[3]) {\n    return distance (position[0], position[1]) < min (delta[0], delta[1])\n    && distance (position[1], position[2]) < min (delta[1], delta[2])\n    && distance (position[2], position[0]) < min (delta[2], delta[0]);\n}\n\nvoid main(void) {\n    float which = mod (index, 3.0);\n    texCoord[0] = uvPosition (index - which);\n    texCoord[1] = uvPosition (index - which + 1.0);\n    texCoord[2] = uvPosition (index - which + 2.0);\n\n    int isheet = int(sheet);\n    if (isheet < sheets) {\n        vec2 pos[3];\n        float delta[3];\n        vec2 values[3*N];\n        readTex (pos, delta, values);\n        if (valid (pos, delta)) {\n            for (int s = 0; s < sheets; s++) {\n                if (s == isheet) {\n                    vec2 valueA = values[s];\n                    int iwhich = int(which);\n                    for (int w = 0; w < 3; w++) {\n                        if (w == iwhich) {\n                            vec2 valueWhich = values[w*N];\n                            float dist, minDist = distance (valueA, valueWhich);\n                            for (int i = 1; i < sheets; i++) {\n                                dist = distance (valueA, values[w*N+i]);\n                                if (dist < minDist)\n                                {\n                                    valueWhich = values[w*N+i];\n                                    minDist = dist;\n                                }\n                            }\n                            posValue = vec4(pos[w], valueWhich);\n                        }\n                    }\n                }\n            }\n        return;\n        }\n    }\n    posValue = vec4(-10.0, -10.0, -10.0, 1.0);\n}\n';
resources['CachedSurface.frag'] = '#ifdef GL_FRAGMENT_PRECISION_HIGH\nprecision highp float;\n#else\nprecision mediump float;\n#endif\nuniform bool clipping;\nvarying vec4 vPos;\nvarying vec2 vValue;\nvec3 hue_to_rgb (in float hue)\n{\n    hue = mod (degrees (hue), 360.0) / 60.0;\n    int h = int (floor (hue));\n    float f = fract (hue);\n    if (h == 0)\n        return vec3 (1.0, f, 0.0);\n    else if (h == 1)\n        return vec3 (1.0 - f, 1.0, 0.0);\n    else if (h == 2)\n        return vec3 (0.0, 1.0, f);\n    else if (h == 3)\n        return vec3 (0.0, 1.0 - f, 1.0);\n    else if (h == 4)\n        return vec3 (f, 0.0, 1.0);\n    else if (h == 5)\n        return vec3 (1.0, 0.0, 1.0 - f);\n}\nfloat sawfct (float x, float dx, float a, float b)\n{\n    return a + (b - a) * fract (x / dx);\n}\nvoid main (void)\n{\n    if (clipping && vPos.y < 0.0)\n        discard;\n    float PI = acos (-1.0);\n    float angle = atan (vValue.y, vValue.x);\n    float blackp = sawfct (angle, PI / 12.0, 0.7, 1.0);\n    float blackm = sawfct (log (length (vValue)), PI / 12.0, 0.7, 1.0);\n    float black = blackp * blackm;\n    gl_FragColor = vec4 (black * hue_to_rgb (angle), gl_FragCoord.z);\n}\n';
