resources['Common.glsl'] = '#ifdef GL_FRAGMENT_PRECISION_HIGH\nprecision highp float;\n#else\nprecision mediump float;\n#endif\n#define EPS 5e-3\n\n/* complex division */\nvec2 cd (in vec2 a, in vec2 b) {\n	return vec2 (a.x * b.x + a.y * b.y, a.y * b.x - a.x * b.y) / dot (b, b);\n}\n\nvec2 cinv (in vec2 z) {\n    return vec2(z.x, -z.y) / dot(z, z);\n}\n\n/* complex square root */\nvec2 csqrt (in vec2 z)\n{\n    float r = sqrt (length (z));\n    float phi = 0.5 * atan (z.y, z.x);\n    return r * vec2 (cos (phi), sin (phi));\n}\n\nvec2 horner (in int n, in vec2 cs[N+1], in vec2 x) {\n    vec2 p;\n    for (int i = N - 1; i >= 0; i--)\n        if (i >= n)\n            p = cs[i];\n        else if (i < n)\n            p = cs[i] + cm(p, x);\n    return p;\n}\n\nvoid weierstrass (in vec2 cs[N+1], out vec2 roots2[N]) {\n	/* polynomial must be monic! */\n    vec2 csn = cs[sheets];\n    if (csn != vec2 (1.0, 0.0))\n        for (int i = 0; i <= sheets; i++)\n            cs[i] = cd (cs[i], csn);\n\n    float maxabs = length (cs[0]);\n    for (int i = 1; i < N; i++)\n        if (i < sheets)\n            maxabs = max (maxabs, length (cs[i]));\n    float upperBound = 1.0 + maxabs; /// length (cs[n]);\n    float phi = 2.0 * acos (-1.0) / float (sheets + 1);\n    float d = upperBound / float (sheets);\n    d = clamp (d, 1.0, 10.0);\n	for (int i = 0; i < sheets; i++)\n        roots2[i] = float (i) * d * vec2 (cos (float (i) * phi), sin (float (i) * phi));\n\n	const int maxiter = 50;\n	bool changing;\n	for (int iter = 0; iter < maxiter; iter++)\n	{\n		changing = false;\n		for (int i = 0; i < sheets; i++)\n        {\n            vec2 result = vec2 (1.0, 0.0);\n            for (int j = 0; j < sheets; j++)\n                if (j != i)\n                    result = cm (result, roots2[i] - roots2[j]);\n            vec2 update = cd (horner (sheets, cs, roots2[i]), result);\n            ///* safe-guard against large update */\n            //if (sheets > 1 && length (update) > 10)\n            //	update /= length (update);\n            if (length (update) > 1e-2)\n                changing = true;\n            roots2[i] -= update;\n        }\n        if (!changing)\n            break;\n	}\n}\n\nvoid quadratic_roots (in vec2 cs[N+1], out vec2 qroots[2]) {\n    if (cs[0] == vec2 (0.0, 0.0)) {\n        qroots[0] = vec2 (0.0, 0.0);\n        qroots[1] = -cd (cs[1], cs[2]);\n    }\n    else\n    {\n        vec2 r = csqrt (cm (cs[1], cs[1]) - 4.0 * cm (cs[2], cs[0]));\n        if (cs[1].x >= 0.0)\n            r = -r;\n        qroots[0] = cd (r - cs[1], 2.0 * cs[2]);\n        qroots[1] = cd (2.0 * cs[0], r - cs[1]);\n    }\n}\n\nvoid roots (in int n, in vec2 cs[N+1], out vec2 roots[N])\n{\n    if (n == 1) {\n        roots[0] = -cd (cs[0], cs[1]);\n        return;\n    }\n    if (n == 2) {\n        vec2 qroots[2];\n        quadratic_roots (cs, qroots);\n        roots[0] = qroots[0];\n        roots[1] = qroots[1];\n        return;\n    }\n    weierstrass (cs, roots);\n    /* selection sort by real part */\n    /*\n    for(int i = 0; i < n - 1; i++)\n    {\n        int jmin = i;\n        for (int j = i + 1; j < n; j++)\n            if (roots[j].x < roots[jmin].x)\n                jmin = j;\n        vec2 z = roots[i];\n        roots[i] = roots[jmin];\n        roots[jmin] = z;\n    }\n    */\n}\n\nvoid f (in vec2 x, out vec2 cs[N+1]);\nvec2 fx (in vec2 x, in vec2 y);\nvec2 fy (in vec2 x, in vec2 y);\nfloat rho (in vec2 x);\nfloat M (in vec2 x, in float rho);\n\nfloat epsilon (in vec2 ys[N]) {\n    float d = distance (ys[0], ys[1]);\n    if (sheets < 2)\n        return 1.0 / 0.0;\n    for (int j = 0; j < sheets; j++) {\n        for (int k = 0; k < sheets; k++) {\n            if (k > j)\n                d = min (d, distance (ys[j], ys[k]));\n        }\n    }\n    return d / 2.0;\n}\n\nfloat Y (in vec2 x, in vec2 ys[N]) {\n    float y = length (cd (fx (x, ys[0]), fy (x, ys[0])));\n    for (int j = 0; j < sheets; j++)\n        y = max (y, length (cd (fx (x, ys[j]), fy (x, ys[j]))));\n    return y;\n}\n\nfloat Delta (in vec2 x, in vec2 ys[N]) {\n    float eps = epsilon (ys);\n    float r = rho (x);\n    float m = M (x, r);\n    float y = Y (x, ys);\n    return r * (sqrt (pow (r * y - eps, 2.0) + 4.0 * eps * m) -\n        (r * y + eps)) / (2.0 * (m - r * y));\n}\n';
resources['DomainColouring.frag'] = 'uniform int sheet;\nvarying vec2 vPosition;\nvec3 hue_to_rgb (in float hue)\n{\n    hue = mod (degrees (hue), 360.0) / 60.0;\n    int h = int (floor (hue));\n    float f = fract (hue);\n    if (h == 0)\n        return vec3 (1.0, f, 0.0);\n    else if (h == 1)\n        return vec3 (1.0 - f, 1.0, 0.0);\n    else if (h == 2)\n        return vec3 (0.0, 1.0, f);\n    else if (h == 3)\n        return vec3 (0.0, 1.0 - f, 1.0);\n    else if (h == 4)\n        return vec3 (f, 0.0, 1.0);\n    else if (h == 5)\n        return vec3 (1.0, 0.0, 1.0 - f);\n}\nfloat sawfct (float x, float dx, float a, float b)\n{\n    return a + (b - a) * fract (x / dx);\n}\nvoid main (void)\n{\n    vec2 cs[N+1];\n    vec2 ys[N];\n    vec2 value;\n    f (vPosition, cs);\n    roots (sheets, cs, ys);\n    int smaller;\n    for (int i = 0; i < sheets; i++)\n    {\n        smaller = 0;\n        for (int j = 0; j < sheets; j++)\n            if (ys[j].x < ys[i].x)\n                smaller++;\n        if (smaller == sheet - 1)\n            value = ys[i];\n    }\n        \n    float PI = acos (-1.0);\n    float angle = atan (value.y, value.x);\n    float blackp = sawfct (angle, PI / 12.0, 0.7, 1.0);\n    float blackm = sawfct (log (length (value)), PI / 12.0, 0.7, 1.0);\n    float black = blackp * blackm;\n    gl_FragColor = vec4 (black * hue_to_rgb(angle), 1.0);\n}\n';
